import {authAPI} from "../api/api";
import {stopSubmit} from "redux-form";

const SET_USER_DATA = 'my-react-project/auth/SET_USER_DATA';
/*—É–Ω–∏–∫–∞–ª–∏–∑–∞—Ü–∏—è –Ω–∞–∑–≤–∞–Ω–∏—è –∫–æ–Ω—Å—Ç–∞–Ω—Ç—ã –¥–ª—è –∏–∑–±–µ–∂–∞–Ω–∏—è —Å–ª—É—á–∞–π–Ω—ã—Ö —Å–æ–≤–ø–∞–¥–µ–Ω–∏–π (—É—Ä–æ–∫ 90)*/

let initialState = {

    id: null,
    email: null,
    login: null,
    isAuth: false
};

export const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state, ...action.payload,
            }

        default:
            return state;
    }
}

export const setAuthUserData = (userId, email, login, isAuth) =>
    ({type: SET_USER_DATA, payload: {userId, email, login, isAuth}})

export const getAuthUserData = () => async (dispatch) => {                        /*–Ω–∞—Å—Ç—Ä–æ–π–∫–∞ –∑–∞–ø—Ä–æ—Å–∞ (—É—Ä–æ–∫ 61)*/
    let response = await authAPI.me();                                            /*async-await (—É—Ä–æ–∫ 90)*/

    if (response.data.resultCode === 0) {
            let {id, email, login } = response.data.data;
            dispatch(setAuthUserData(id, email, login, true));
        }
}

export const login = (email, password, rememberMe, data) => async (dispatch) => {
    let response = await authAPI.login(email, password, rememberMe, data);

        if (response.data.resultCode === 0) {                                     /*login –∏ logout (—É—Ä–æ–∫ 78)*/
            dispatch(setAuthUserData());

        }else {
            let message = response.data.messages.length > 0
                ? response.data.messages[0]
                : "Some error";
            dispatch(stopSubmit("login", {_error: message}));                       /*stopSubmit (—É—Ä–æ–∫ 79)*/
        }
}

export const logout = () => async (dispatch) => {
    let response = await authAPI.logout();

    if (response.data.resultCode === 0) {                                            /*login –∏ logout (—É—Ä–æ–∫ 78)*/
        dispatch(setAuthUserData(null, null, null, false));
    }
}


export default authReducer;
