import React from "react";
import {reduxForm} from "redux-form";
import {createField, Input} from "../common/FormsControls/FormsControls";
import {required} from "../../utils/validators/validators";
import {connect} from "react-redux";
import {login} from "../../redux/auth-reducer";
import {Redirect} from "react-router-dom";


const LoginForm = ({handleSubmit, error}) => {                               /*–¥–µ—Å—Ç—Ä—É–∫—Ç—É—Ä–∏–∑–∞—Ü–∏—è –ø–∞—Ä–∞–º–µ—Ç—Ä–æ–≤ (—É—Ä–æ–∫ 90)*/

    return (
        <form onSubmit={handleSubmit}>

            {createField("Email", "email", [required], Input )}

            {/*<div>*/}
            {/*    <Field placeholder={"Email"} name={"email"} component={Input}*/}
            {/*           validate={required}/>*/}
            {/*</div>*/}

            {createField("Password", "password", [required], Input, {type: "password"})}

            {/*<div>*/}
            {/*    <Field placeholder={"Password"} name={"password"} type={"password"}*/}
            {/*           component={Input} validate={required}/>*/}
            {/*</div>*/}

            {createField( null, "rememberMe",[], Input, {type: "checkbox"}, "remember me")}
            {/*<div>*/}
            {/*    <Field component={Input} name={"rememberMe"} type={"checkbox"}/> remember me*/}
            {/*</div>*/}

            {error && <div className="formSummaryError">
                {error}
            </div>}
            <div>
                <button>Submit</button>
            </div>
        </form>
    )
}

const LoginReduxForm = reduxForm({form: 'login'})(LoginForm)
/*–∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–Ω–∞—è –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ (HOC), –∫–æ—Ç–æ—Ä—É—é —Ñ–æ—Ä–º–∏—Ä—É–µ—Ç redux-form (—É—Ä–æ–∫ 75) */

const Login = (props) => {

    const onSubmit = (forData) => {
        props.login(forData.email, forData.password, forData.rememberMe)
    }

    if (props.isAuth) {
        return <Redirect to={"/profile"}/>
    }else
        return (
        <div>
            <h1>LOGIN</h1>
            <LoginReduxForm onSubmit={onSubmit}/>
        </div>
    )
}


const mapStateToProps = (state) => ({
    isAuth: state.auth.isAuth
})

export default connect(mapStateToProps, {login} )(Login);
/*—Ñ-—Ü–∏—è connect() –∏–∑ –ø–∞–∫–µ—Ç–∞ react-redux –æ–±–æ—Ä–∞—á–∏–≤–∞–µ—Ç Login –≤ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–Ω—É—é –∫–æ–º–ø–æ–Ω–µ–Ω—Ç—É*/
