import * as axios from "axios";


const instance = axios.create({
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    headers: {
        "API-KEY": "583f6d53-e1e5-4788-bc99-443ecbea87ff"
    }
});

export const usersAPI = {
    getUsers(currentPage = 1, pageSize = 5) {

        return instance.get(`users?page=${currentPage}&count=${pageSize}`)       /*get –ø–∞—Ä–∞–º–µ—Ç—Ä —É—Ä–æ–∫ 72*/
            .then(response => {
                return response.data;                                            /*–ø–æ—á–∏—Ç–∞—Ç—å –æ promise (–£—Ä–æ–∫ 63)*/
            });
    },

    follow(userId) {
        return instance.post(`follow/${userId}`)                                  /*URI –ø–∞—Ä–∞–º–µ—Ç—Ä - —á–∞—Å—Ç—å URL  —É—Ä–æ–∫ 72*/
    },


     unfollow(userId) {
         return instance.delete(`follow/${userId}`)
     }
     ,

     getProfile(userId) {
        console.warn('Obsolet method. Please profileAPI object.');                /*BWC - —É—Ä–æ–∫ 73*/
         return profileAPI.getProfile(userId);

     }
}

export const profileAPI = {
    getProfile(userId) {
        return instance.get(`profile/` + userId);
    },
    getStatus(userId) {
        return instance.get(`profile/status/` + userId);
    },
    updateStatus( status) {
        return instance.put(`profile/status/`, {status: status});        /*put - –∑–∞–ø—Ä–æ—Å –æ—Ç–ø—Ä–∞–≤–ª—è–µ—Ç –Ω–∞ —Å–µ—Ä–≤–µ—Ä –æ–±—ä–µ–∫—Ç*/
    }
}

export const authAPI = {
    me() {
        return instance.get(`auth/me`);                                            /*–Ω–∞—Å—Ç—Ä–æ–π–∫–∞ –∑–∞–ø—Ä–æ—Å–∞ (—É—Ä–æ–∫ 61)*/
    },
    login(email, password, rememberMe = false) {
        return instance.post('auth/login', {email, password, rememberMe});          /*login –∏ logout (—É—Ä–æ–∫ 78)*/
    },
    logout() {
        return instance.delete('auth/login');
    }
}